import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Modal,
  Tabs
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'
const confirm = Modal.confirm
const {TabPane} = Tabs

@connect(
  state => ({...state.inventoryPlan}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }

  // 查询数据
  query = (tableActiveKey) => {
    const {handleGetList, searchParams, size, update} = this.props
    let reqObj = {...searchParams}
    console.log(tableActiveKey, 666)
    update({tableActiveKey})
    handleGetList({searchParams: {...reqObj, page: 1, size, statusCode: tableActiveKey}}, true)
  }

  //创建盘点计划
  createPlan = () => {
    const {update} = this.props
    update({isShowDeliveryModal: true, modalSearchResult: [], modalTotal: 0})
  }

  //取消盘点计划
  closePlan = () => {
    const {selectedRowKeys, selectedRows, handleCloseOrder, handleGetList, searchParams, size, tableActiveKey} = this.props
    if (!selectedRowKeys.length) {
      message.error('请选择您要取消的盘点计划')
      return
    }
    confirm({
      content: '是否确认取消？',
      onOk() {
        let array = []
        selectedRows.map(o => array.push(o.orderCode))
        handleCloseOrder({data: {orderCodeList: array}}, () => {
          message.success('盘点计划取消成功')
          let reqObj = {...searchParams}
          handleGetList({searchParams: {...reqObj, page: 1, size, statusCode: tableActiveKey}})
        })
      }
    })
  }

  //开始盘点
  beginPlan = () => {
    const {selectedRowKeys, selectedRows, searchParams, tableActiveKey, size, handleStart, handleGetList} = this.props
    if (!selectedRowKeys.length) {
      message.error('请选择您要开始的盘点计划')
      return
    }
    if (selectedRowKeys.length && selectedRowKeys.length > 1) {
      message.error('您只能选择一个计划单号进行盘点开始')
      return
    }
    handleStart({searchParams: {orderCode: selectedRows[0].orderCode}}, data => {
      if (data) {
        message.success('开始盘点')
      }
      let reqObj = {...searchParams}
      handleGetList({searchParams: {...reqObj, page: 1, size, statusCode: tableActiveKey}})
    })
  }

  //盘点确认
  detPlan = () => {
    const {selectedRowKeys, selectedRows, handleConfirmPlan, searchParams, handleGetList, tableActiveKey, size} = this.props
    if (!selectedRowKeys.length) {
      message.error('请选择您要确认的盘点计划')
      return
    }
    if (selectedRowKeys.length && selectedRowKeys.length > 1) {
      message.error('您只能选择一个计划单号进行盘点确认')
      return
    }
    handleConfirmPlan({searchParams: {orderCode: selectedRows[0].orderCode}}, () => {
      message.success('盘点确认成功')
      let reqObj = {...searchParams}
      handleGetList({searchParams: {...reqObj, page: 1, size, statusCode: tableActiveKey}})
    })
  }

  render() {
    const {tableActiveKey, tab0, tab1, tab2, tab3, tab4} = this.props
    return (
      <div className={style['table-tool']} style={{paddingBottom: 0}}>
        <Button type="primary" className={style.btn} onClick={this.createPlan}>创建盘点计划</Button>
        {
          tableActiveKey != 4 ?
          <React.Fragment>
            <Button type="primary" className={style.btn} onClick={this.closePlan}>取消盘点计划</Button>
            <Button type="primary" className={style.btn} onClick={this.beginPlan}>开始盘点</Button>
            <Button type="primary" className={style.btn} onClick={this.detPlan}>盘点确认</Button>
          </React.Fragment>
          : null
        }

        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`初始(${tab0 || 0})`} key="0"></TabPane>
          <TabPane tab={`盘点中(${tab1 || 0})`} key="1"></TabPane>
          <TabPane tab={`盘点完成(${tab2 || 0})`} key="2"></TabPane>
          <TabPane tab={`盘点确认(${tab3 || 0})`} key="3"></TabPane>
          <TabPane tab={`已取消(${tab4 || 0})`} key="4"></TabPane>
        </Tabs>
      </div>
    )
  }
}



