import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  Input,
  InputNumber,
  message,
  Form
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxMultipleSelect, CsxMultipleSearch} from 'Form'
import {
  API_PRODUCTS_KEYVALUES,
  API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES,
  API_STORAGELOCATIONS_GETSTORAGELOCATIONFUZZYKEYVALUES,
  API_PRODUCT_GETPRODUCTKEYVALUES,
  API_PRODUCT_CATEGORYSONS,
  API_STORAGELOCATIONS_RESERVOIRAREAS
} from 'api'
import {withRouter} from 'react-router'
import {popupFormItemLayoutLabel8} from 'enum'
const FormItem = Form.Item

@connect(
  (state) => ({...state.inventoryPlan, userLoation: state.userLoation}),
  mapDispatchToProps
)
class app extends PureComponent {
  constructor() {
    super()
    this.state = {}
    this.columns = [
      {
        title: '商品编码',
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '商品名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '单位',
        width: 100,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '库区',
        width: 150,
        dataIndex: 'reservoirAreaName',
        key: 'reservoirAreaName',
        render: (text, record, index) => (
          <span>{`${record.reservoirAreaCode}-${record.reservoirAreaName}`}</span>
        )
      },
      {
        title: '储位',
        width: 150,
        dataIndex: 'storeLocationName',
        key: 'storeLocationName',
        render: (text, record, index) => (
          <span>{`${record.storeLocationCode}-${record.storeLocationName}`}</span>
        )
      }
    ]
  }
  // componentDidMount() {
  //   this._getAsyncData()
  // }

  // // 根据tabs的key来判断请求哪个接口
  // _getAsyncData() {
  //   const {handleCreatePlan} = this.props
  //   handleCreatePlan()
  // }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowDeliveryModal: false, deliveryData: {}})
  }

  onInputChange(values) {
    const {update, deliveryData} = this.props
    console.log(values)
    update({deliveryData: {...deliveryData, ...values}})
  }

  onInputCreateChange(values) {
    const {update, createPlanData} = this.props
    console.log(values)
    update({createPlanData: {...createPlanData, ...values}})
  }

  // 查询数据
  query = () => {
    const {handleCreatePlan, deliveryData} = this.props
    let reqObj = {...deliveryData}
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleCreatePlan({searchParams: {...reqObj}})
  }

  // 重置
  reset = () => {
    const {update, handleCreatePlan} = this.props
    update(
      {deliveryData: {}, createPlanData: {}},
      (modalSearchResult: []),
      (modalTotal: 0)
    )
    handleCreatePlan({searchParams: {}, warehouseCode: {}})
  }

  // 生成盘点计划
  savePlan = () => {
    const {
      createPlanData,
      modalSearchResult,
      handleCreateData,
      update,
      form
    } = this.props
    const {searchParams, handleGetList, tableActiveKey, size} = this.props
    let item = []
    modalSearchResult.map((o) =>
      item.push({
        productCode: o.productCode,
        storeLocationCode: o.storeLocationCode
      })
    )
    if (!item.length) {
      message.error('盘点的商品为空，不能进行生成盘点计划操作，请先查询商品')
      return
    }
    form.validateFieldsAndScroll((err, values) => {
      console.log('err is', err)
      if (!err) {
        handleCreateData(
          {
            data: {
              items: item,
              remark: createPlanData.remark,
              claimMax: createPlanData.claimMax
            }
          },
          (data) => {
            Modal.success({
              content: `盘点计划创建成功，计划单号：${data.orderCode}`,
              onOk() {
                update({
                  isShowDeliveryModal: false,
                  deliveryData: {},
                  createPlanData: {}
                })
                let reqObj = {...searchParams}
                handleGetList({searchParams: {...reqObj, page: 1, size}}, false)
              }
            })
          }
        )
      }
    })
    // const {claimMax = 0} = createPlanData
    // const lessThanFive = /^[0-9]\d{0,4}$/
    // if (!lessThanFive.test(claimMax)) {
    //   message.error('任务领取数量不能大于五位整数')
    //   return false
    // }
    // // return 123
  }

  render() {
    const {
      deliveryData: {
        productCode,
        rootCategoryCode,
        centreCategoryCode,
        bigCategoryCode,
        storeLocationCode,
        reservoirAreaCode
      },
      createPlanData: {remark, claimMax},
      userLoation
    } = this.props

    const {modalTableLoading, modalSearchResult, modalTotal, form} = this.props
    const {getFieldDecorator} = form

    return (
      <Modal
        title="创建盘点计划"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1024}
      >
        <Row>
          <Col span={12}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label="商品编码"
              searchParamsKey="productCode"
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{
                data: productCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={12}>
            <Row type="flex">
              <CsxMultipleSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label="商品分类"
                placeholder="部类"
                searchParams={{type: '1'}}
                selectItem={{
                  data: rootCategoryCode,
                  key: 'categoryCode',
                  valueKey: 'categoryName'
                }}
                onChangeSelect={(value) => {
                  this.onInputChange({rootCategoryCode: value})
                }}
                style={{marginLeft: 5, width: 120}}
              />
              <CsxMultipleSelect
                placeholder="大类"
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{
                  parCode: rootCategoryCode && rootCategoryCode.value,
                  level: '2'
                }}
                selectItem={{
                  data: bigCategoryCode,
                  key: 'categoryCode',
                  valueKey: 'categoryName'
                }}
                onChangeSelect={(value) => {
                  this.onInputChange({bigCategoryCode: value})
                }}
                noReady
                errMessage="请先选择部类"
                style={{marginLeft: 10, width: 120}}
              />
              <CsxMultipleSelect
                placeholder="中类"
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{
                  parCode: bigCategoryCode && bigCategoryCode.value,
                  level: '3'
                }}
                selectItem={{
                  data: centreCategoryCode,
                  key: 'categoryCode',
                  valueKey: 'categoryName'
                }}
                onChangeSelect={(value) => {
                  this.onInputChange({centreCategoryCode: value})
                }}
                noReady
                errMessage="请先选择大类"
                style={{marginLeft: 10, width: 120}}
              />
            </Row>
          </Col>
          <Col span={12}>
            <CsxMultipleSelect
              dateApi={API_STORAGELOCATIONS_RESERVOIRAREAS}
              label="库区"
              selectItem={{
                data: reservoirAreaCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={12}>
            <CsxMultipleSearch
              dateApi={API_STORAGELOCATIONS_GETSTORAGELOCATIONFUZZYKEYVALUES}
              label="储位"
              searchParamsKey="storageLocationCode"
              // searchParams={{warehouseCode: userLoation.value}}
              selectItem={{
                data: storeLocationCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onSelect={(value) => {
                this.onInputChange({storeLocationCode: value})
              }}
              onChangeSelect={(value) => {
                this.onInputChange({storeLocationCode: value})
              }}
            />
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end">
              <span style={{marginTop: '6px', marginRight: '20px'}}>
                查询结果：<span style={{fontWeight: 'bold'}}>{modalTotal}</span>
              </span>
              <Button
                type="primary"
                style={{marginLeft: '10px'}}
                onClick={this.query}
              >
                查询
              </Button>
              <Button
                type="primary"
                style={{marginLeft: '10px'}}
                onClick={this.reset}
              >
                重置
              </Button>
            </Row>
          </Col>
          <Col span={12}>
            <div style={{marginTop: '15px'}} className="commonInput">
              <label>备注：</label>
              <div className="box">
                <Input
                  value={remark}
                  onChange={(e) => {
                    this.onInputCreateChange({remark: e.target.value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={12}>
            <div style={{marginTop: '15px'}} className="commonInput">
              {/* <label>任务领取数量：</label>
              <div className="box">
                <InputNumber
                  style={{width: '200px'}}
                  value={claimMax}
                  onChange={(data) => {
                    this.onInputCreateChange({claimMax: data})
                  }}
                />
              </div> */}
              <FormItem
                {...popupFormItemLayoutLabel8}
                label="任务领取数量"
                validateFirst={true}
              >
                {getFieldDecorator('claimMax', {
                  initialValue: claimMax,
                  rules: [
                    {
                      validator(rule, value) {
                        if (Number(value) < 0) {
                          return Promise.reject('不能输入负数')
                        }
                        if (!value) {
                          return Promise.resolve()
                        }
                        if (!/^[0-9]\d{0,4}$/.test(value)) {
                          return Promise.reject('请输入不超过五位的整数')
                        }
                        return Promise.resolve()
                      }
                    }
                  ]
                })(
                  <InputNumber
                    style={{width: '200px'}}
                    // value={claimMax}
                    onChange={(data) => {
                      this.onInputCreateChange({claimMax: data})
                    }}
                  />
                )}
              </FormItem>
            </div>
            <p style={{fontSize: '12px', color: 'red', marginTop: '6px'}}>
              *任务领取数量，不填默认领取数量为30
            </p>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end">
              <Button
                type="primary"
                style={{
                  marginLeft: '10px',
                  marginTop: '5px',
                  marginBottom: '5px'
                }}
                onClick={this.savePlan}
              >
                生成盘点计划
              </Button>
            </Row>
          </Col>
          <Col span={24}>
            <div className="table-group" style={{marginTop: '15px'}}>
              <Table
                rowKey={(record) => record.id}
                loading={modalTableLoading}
                columns={this.columns}
                dataSource={modalSearchResult}
                scroll={{x: 'max-content', y: 400}}
                pagination={false}
              />
            </div>
          </Col>
        </Row>
      </Modal>
    )
  }
}
export default Form.create()(withRouter(app))



