import * as actionType from './actionType'
import {getData} from 'util'
import {API_INVENTORYPLAN_GETHEADLIST_PAGE, API_INVENTORYPLAN_GETTRACELIST_TPAGE, API_INVENTORYPLAN_PLANITEMEXPORT_EXPORT, API_INVENTORYPLAN_CREATEINVENTORYPLAN_CREATE, API_INVENTORYPLAN_CANSOLEPLAN_CLOSE, API_INVENTORYPLAN_CONFIRMPLAN_DET, API_INVENTORYPLAN_STARTPLAN_START, API_INVENTORYPLAN_STOCKDETAILS_TPAGE, API_INVENTORYPLAN_GETCOUNTBYSTATUSSORT_NUM, API_INVENTORYPLAN_GETITEMLIST_PAGE, API_INVENTORYPLAN_REPEATINVENTORY_REP, API_INVENTORYPLAN_GETITEMPRODUCTTRACE_TPAGE} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.INVENTORYPLAN_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData, status) => (dispatch) => {
  console.log(status, 333)
  dispatch({type: actionType.INVENTORYPLAN_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      i === 'productName' ? reqObjSearchParams[i] = reqObjSearchParams[i].children : reqObjSearchParams[i] = reqObjSearchParams[i].value
      // reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.beginDate = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.endDate = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate
  reqData.searchParams = reqObjSearchParams

  if (status) {
    getData({api: API_INVENTORYPLAN_GETHEADLIST_PAGE, ...reqData}).then(data => {
      // 相应的逻辑判断
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.INVENTORYPLAN_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          selectedRowKeys: [], // 选中的队列key
          selectedRows: [], // 选中的队列数据
          searchResult: data.result.list || []
        }
      })
    }).catch(err => {
      dispatch({
        type: actionType.INVENTORYPLAN_FILE
      })
    })
  } else {
    // 查询统计数量tabs
    console.log(reqData, 222)
    getData({api: API_INVENTORYPLAN_GETCOUNTBYSTATUSSORT_NUM, ...reqData}).then(({result}) => {
      // 相应的逻辑判断
      let arr = ['0', '1', '2', '3', '4']
      let status = ''
      let tabNumObj = {
        tab0: '',
        tab1: '',
        tab2: '',
        tab3: '',
        tab4: ''
      }
      result.map((o) => {
        tabNumObj[`tab${o.key}`] = o.value
      })
      for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < result.length; j++) {
          if (result[j].key == arr[i] && result[j].value) {
            status = arr[i]
            break
          }
        }
        if (status) break
      }
      if (status) {
        console.log(status, 77)
        reqData.searchParams.statusCode = status
      }
      getData({api: API_INVENTORYPLAN_GETHEADLIST_PAGE, ...reqData}).then(data => {
        // 相应的逻辑判断
        let {page, size} = reqData.searchParams
        dispatch({
          type: actionType.INVENTORYPLAN_UPDATE,
          data: {
            page,
            size,
            tableLoading: false,
            total: data.result.total,
            tableActiveKey: status,
            selectedRowKeys: [], // 选中的队列key
            selectedRows: [], // 选中的队列数据
            searchResult: data.result.list || [],
            ...tabNumObj
          }
        })
      }).catch(err => {
        dispatch({
          type: actionType.INVENTORYPLAN_FILE
        })
      })
    }).catch(err => {
      dispatch({
        type: actionType.INVENTORYPLAN_FILE
      })
    })
  }
}

// 创建盘点计划的列表
export const handleCreatePlan = (reqData, CallBack) => (dispatch) => {
  dispatch({type: actionType.INVENTORYPLAN_UPDATE, data: {modalTableLoading: true}})
  getData({api: API_INVENTORYPLAN_STOCKDETAILS_TPAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.INVENTORYPLAN_UPDATE,
      data: {
        modalTableLoading: false,
        modalTotal: data.result.length,
        modalSearchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYPLAN_FILE
    })
  })
}

// 生成盘点计划
export const handleCreateData = (reqData, callBack) => (dispatch) => {
  getData({api: API_INVENTORYPLAN_CREATEINVENTORYPLAN_CREATE, ...reqData}).then(data => {
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYPLAN_FILE
    })
  })
}

