//盘点计划详情主列表查询
export const handleDetailPlan = (reqData, failCallBack) => (dispatch) => {
  dispatch({type: actionType.INVENTORYPLAN_UPDATE, data: {modalTableLoading: true}})
  getData({api: API_INVENTORYPLAN_GETITEMLIST_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.INVENTORYPLAN_UPDATE,
      data: {
        modalPage: page,
        modalSize: size,
        modalTableLoading: false,
        modalTotal: data.result.total,
        modalSearchResult: data.result.list || [],
        modalSelectedRowKeys: [], // 选中的队列key
        modalSelectedRows: [] // 选中的队列数据
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYPLAN_FILE
    })
    failCallBack && failCallBack()
  })
}

