import React, {PureComponent} from 'react'
import {
  Modal,
  Table
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.inventoryPlan}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '操作类型',
      width: 100,
      dataIndex: 'operateTypeDesc',
      key: 'operateTypeDesc'
    }, {
      title: '操作人',
      width: 100,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '操作时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetailRecord, modalId} = this.props
    console.log(modalId, 111)
    handleGetDetailRecord({searchParams: {orderCode: modalId}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowRecordModal: false})
  }
  render() {
    const {deliveryTableLoading, searchResultRecord} = this.props
    return (
        <Modal
          title="操作记录"
          visible={true}
          onCancel={this.handleCloseModal}
          footer={null}
          mask={false}
          width={777}
        >
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={(record, index) => index}
              loading={deliveryTableLoading}
              columns={this.columns}
              scroll={{x: 700, y: 400}}
              dataSource={searchResultRecord}
              pagination={false}
            />
          </div>
        </Modal>
    )
  }
}



