import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  message,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect} from 'Form'
import moment from 'moment'
// import {PROCUREMENT} from 'Mock'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.inventoryPlan, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  // 设置默认的时间  前一周 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preWeek = moment().subtract(7, 'd').startOf('day')
    update({searchParams: {initDate: [preWeek, moment().endOf('day')]}})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    const {initDate = []} = searchParams
    // 判断创建时时间是否为空
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('请选择创建时间后搜索')
    }
    handleGetList({searchParams: {...reqObj, page: 1, size}}, false)
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
     // 重置时间
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        initDate,
        orderCode,
        statusCode
      }
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>计划单号：</label>
              <div className='box'>
                <Input value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          {/* <Col span={6}>
            <CsxSelect
              label='状态'
              data={[{key: 0, value: '初始'}, {key: 1, value: '待盘点'}, {key: 2, value: '已完成'}, {key: 3, value: '已确认'}, {key: 4, value: '已取消'}]}
              selectItem={{data: statusCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({statusCode: value})
              }}
            />
          </Col> */}
          <Col span={12}>
            <div className='commonInput'>
              <label>创建日期：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



