import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  Icon,
  Popover
} from 'antd'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'
import {connect} from 'react-redux'
const confirm = Modal.confirm
@connect(
  state => ({...state.inventoryPlan, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 100,
      dataIndex: 'defaultStorageLocationFlag',
      key: 'defaultStorageLocationFlag',
      render: (text, record, index) => <div className="tableOperation">
        <span onClick={() => {
          this.onEdit('isShowRecordModal', record.orderCode)
        }}>记录</span>
      </div>
    }, {
      title: '计划单号',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode',
      render: (text, record, index) => <React.Fragment><a onClick={() => {
        const {history: {location}} = this.props
        window.open(`${location.pathname}/${text}`)
        // history.push(`${location.pathname}/${record.orderCode}`)
      }}>{text}</a><Popover content={<img id={`key${text}`} style={{width: '221px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '状态',
      width: 100,
      dataIndex: 'statusCodeDesc',
      key: 'statusCodeDesc'
    }, {
      title: '盘点差异金额（含税）',
      width: 100,
      dataIndex: 'inventoryAmountDiffSum',
      key: 'inventoryAmountDiffSum'
    }, {
      title: '盘点差异金额（不含税）',
      width: 100,
      dataIndex: 'inventoryAmountDiffNoTaxSum',
      key: 'inventoryAmountDiffNoTaxSum'
    }, {
      title: '是否有复盘',
      width: 100,
      dataIndex: 'repeatInventoryFlagDesc',
      key: 'repeatInventoryFlagDesc'
    }, {
      title: '任务最大领取数',
      width: 100,
      dataIndex: 'claimMax',
      key: 'claimMax'
    }, {
      title: '备注',
      width: 100,
      dataIndex: 'remark',
      key: 'remark'
    }]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }

  // 打印条形码
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  //一些操作
  onEdit = (modalType, id) => {
    const {update} = this.props
    update({[modalType]: true, modalId: id})
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, tableActiveKey} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, receiveStatus: tableActiveKey, ...searchParams, isAsc: true, columns: sorter.field}}, true)
      } else {
        handleGetList({searchParams: {page, size, receiveStatus: tableActiveKey, ...searchParams, isAsc: false, columns: sorter.field}}, true)
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, receiveStatus: tableActiveKey, ...searchParams}}, true)
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      },
      selectedRowKeys,
      getCheckboxProps: record => ({
        // disabled: record.receiveStatus == '2' // Column configuration not to be checked
      })
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



